import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';

import {
  CssBaseline,
  Container,
  Typography,
  IconButton,
  Grid,
  TextField,
  FormControlLabel,
  Checkbox,
  ExpansionPanel,
  ExpansionPanelDetails,
  ExpansionPanelSummary
} from '@material-ui/core';

import {
  PlayCircleFilled,
  PauseCircleFilled,
  ExpandMore
} from '@material-ui/icons';

import { Howl } from 'howler';

import Slider from '@material-ui/lab/Slider';

const PrettoSlider = withStyles({
  root: {
    color: '#52af77',
    height: 8,
  },
  thumb: {
    height: 24,
    width: 24,
    backgroundColor: '#fff',
    border: '2px solid currentColor',
    marginTop: -8,
    marginLeft: -12,
    '&:focus,&:hover,&$active': {
      boxShadow: 'inherit',
    },
  },
  active: {},
  valueLabel: {
    left: 'calc(-50% + 4px)',
  },
  track: {
    height: 8,
    borderRadius: 4,
  },
  rail: {
    height: 8,
    borderRadius: 4,
  },
})(Slider);

const styles = theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  form: {
    width: '100%',
    marginTop: theme.spacing(3),
  },
  bpmValue: {
    paddingRight: '5px'
  },
  smallSpacing: {
    marginTop: '25px'
  },
  mediumSpacing: {
    marginTop: '35px'
  },
  controlButtons: {
    fontSize: '5rem'
  }
});

class App extends Component {
  constructor() {
    super();

    this.state = {
      isPlaying: false,
      actualBpm: 60,
      desiredBpm: 60,
      isProgressive: false,
      startValue: 40,
      endValue: 80,
      step: 5,
      delay: 30,
      currentTick: 0,
      isSettingsOpen: document.documentElement.clientWidth > 600
    };

    this.tickNormal = this.makeHowl('data:audio/flac;base64, ');
    this.tickFourth = this.makeHowl('data:audio/flac;base64, ');

    this.resetTimer();
    this.resetProgressiveTimer();
  }

  makeHowl(src) {
    return new Howl({
      autoplay: false,
      loop: false,
      src: [src]
    });
  }

  tick = () => {
    if (!this.state.isPlaying) return;

    const tickSound = this.state.currentTick % 4 === 0 ? this.tickFourth : this.tickNormal;
    tickSound.play();

    this.setState({
      currentTick: this.state.currentTick + 1
    });

    if (this.state.desiredBpm !== this.state.actualBpm) {
      this.setState({
        actualBpm: this.state.desiredBpm
      }, this.resetTimer);
    }
  }

  tickBpmIncrement = () => {
    if (!this.state.isProgressive) return;
    if (!this.state.isPlaying) return;

    if (this.state.actualBpm < this.state.endValue) {
      const increment = Math.min(this.state.step, this.state.endValue - this.state.actualBpm);
      this.setState({
        desiredBpm: this.state.actualBpm + increment
      });
    }
  }

  resetTimer = () => {
    if (this._tickInterval) {
      clearInterval(this._tickInterval);
    }

    this._tickInterval = setInterval(this.tick, Math.ceil(60 / this.state.actualBpm * 1000));
  }

  resetProgressiveTimer = () => {
    if (this._progressiveInterval) {
      clearInterval(this._progressiveInterval);
    }

    this._progressiveInterval = setInterval(this.tickBpmIncrement, this.state.delay * 1000);
  }

  handleChangeProgressive = (event) => {
    const progressiveEnabled = !this.state.isProgressive;
    let desiredBpm = this.state.desiredBpm;
    if (progressiveEnabled) desiredBpm = this.state.startValue;
    this.setState({
      isProgressive: progressiveEnabled,
      desiredBpm
    }, () => {
      if (progressiveEnabled) this.resetProgressiveTimer();
    });
  }

  handleChangeStartValue = (event) => {
    const value = Number(event.target.value);
    if (value <= 0) return;
    this.setState({
      startValue: value
    });
  }

  handleChangeEndValue = (event) => {
    const value = Number(event.target.value);
    if (value <= 0) return;
    this.setState({
      endValue: value
    });
  }

  handleChangeStep = (event) => {
    const value = Number(event.target.value);
    if (value <= 0) return;
    this.setState({
      step: value
    });
  }

  handleChangeDelay = (event) => {
    const value = Number(event.target.value);
    if (value <= 0) return;
    this.setState({
      delay: value
    }, this.resetProgressiveTimer);
  }

  handleStartPlaying = () => {
    let desiredBpm = this.state.desiredBpm;
    if (this.state.isProgressive) desiredBpm = this.state.startValue;
    this.setState({
      isPlaying: true,
      desiredBpm
    });
  }

  handleStopPlaying = () => {
    this.setState({
      isPlaying: false
    });
  }

  handleSetBpm = (event, value) => {
    this.setState({
      desiredBpm: value
    });
  }

  handleToggleSettings = () => {
    this.setState({
      isSettingsOpen: !this.state.isSettingsOpen
    });
  }

  renderControls() {
    if (this.state.isPlaying) {
      return (
        <IconButton color="primary" onClick={this.handleStopPlaying}>
          <PauseCircleFilled classes={{root: this.props.classes.controlButtons}} fontSize="large" />
        </IconButton>
      );
    } else {
      return (
        <IconButton color="primary" onClick={this.handleStartPlaying}>
          <PlayCircleFilled classes={{root: this.props.classes.controlButtons}} fontSize="large" />
        </IconButton>
      );
    }
  }

  render() {
    const classes = this.props.classes;

    return (
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <div className={classes.paper}>
          <Typography component="h1" variant="h3">
            Metronome
          </Typography>
          <div className={classes.smallSpacing}></div>
          <div>
            <Typography component="h1" variant="h4" display="inline" classes={{h4: classes.bpmValue}}>
              {this.state.desiredBpm}
            </Typography>
            <Typography component="h1" variant="h6" display="inline">
              BPM
            </Typography>
          </div>
          <PrettoSlider value={this.state.desiredBpm} min={35} max={180} onChange={this.handleSetBpm} />
          <div className={classes.mediumSpacing}></div>
          <ExpansionPanel expanded={this.state.isSettingsOpen} onChange={this.handleToggleSettings}>
            <ExpansionPanelSummary
              expandIcon={<ExpandMore />}
              aria-controls="panel1bh-content"
              id="panel1bh-header"
            >
              <Typography className={classes.heading}>Settings</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <form className={classes.form} noValidate>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <FormControlLabel
                      control={<Checkbox checked={this.state.isProgressive} onChange={this.handleChangeProgressive} value="progressiveMetronome" color="primary" />}
                      label="Increase speed"
                    />
                  </Grid>
                  <Grid item xs={12} sm={3}>
                    <TextField
                      name="startValue"
                      type="number"
                      variant="outlined"
                      fullWidth
                      id="startValue"
                      label="Start"
                      autoFocus
                      value={this.state.startValue}
                      onChange={this.handleChangeStartValue}
                    />
                  </Grid>
                  <Grid item xs={12} sm={3}>
                    <TextField
                      name="endValue"
                      type="number"
                      variant="outlined"
                      fullWidth
                      id="endValue"
                      label="End"
                      autoFocus
                      value={this.state.endValue}
                      onChange={this.handleChangeEndValue}
                    />
                  </Grid>
                  <Grid item xs={12} sm={3}>
                    <TextField
                      name="step"
                      type="number"
                      variant="outlined"
                      fullWidth
                      id="step"
                      label="Step"
                      autoFocus
                      value={this.state.step}
                      onChange={this.handleChangeStep}
                    />
                  </Grid>
                  <Grid item xs={12} sm={3}>
                    <TextField
                      name="delay"
                      type="number"
                      variant="outlined"
                      fullWidth
                      id="delay"
                      label="Delay"
                      autoFocus
                      value={this.state.delay}
                      onChange={this.handleChangeDelay}
                    />
                  </Grid>
                </Grid>
              </form>
            </ExpansionPanelDetails>
          </ExpansionPanel>
          <div className={classes.smallSpacing}></div>
          {this.renderControls()}
        </div>
      </Container>
    );
  }
}

export default withStyles(styles)(App);
